@import "common.css";
.non-visible {
	display: none;
}
.visible {
	display: block;
}
.container {
	max-width: 1300px;
	margin: 0 auto;
}
@media(max-width: 1024px) {
	.container {
		padding-left: 10px;
		padding-right: 10px;
	}
	.nav {
		padding-left: 10px;
		padding-right: 10px;
	}
}
.back_nav {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  width: 100%;
  z-index: 2;
}
.nav {
	max-width: 1300px;
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
	padding-top: 30px;
	padding-bottom: 30px;
	z-index: 2;

}
.nav ul {
	display: flex;
}
.nav p a {
	font-family: 'Lora', sans-serif;
	font-weight: 700;	
	font-size: 20px;
	color: #F8F9FA;
	}
.nav ul li:not(last-child) {
	margin-left: 20px;
}
.nav ul li a {
	color: #E5E5E5;
	font-size: 14px;
	padding-bottom: 5px;
}
main {
	height: 600px;
   background: url('../img/article_back.jpg') 0 0/100% 100% no-repeat;
}
.related-post {
	margin-top: 100px;
}
.related-post .container h2 {
	color: #495057;
	font-size: 36px;
}
.related-post .container ul {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin-top: 40px;
}
.related-post .container ul li {
	width: 420px;
	height: 350px;
	background: url('../img/related_post/1.jpg') 0 0/100% 100% no-repeat;
	margin-left: 7px;
	margin-top: 7px;
	display: flex;
	align-items: flex-end;
}
.related-post .container ul li p {
	font-size: 12px;
	color: #E5E5E5;
}
.related-post .container ul li h4 {
	color: #FFFFFF;
	font-size: 18px;
	margin-top: 20px;
	margin-bottom: 10px;
}
.related_li {
	margin-left: 40px;
	width: 270px;
}
.related_li p:last-child {
	margin-bottom: 40px;
}
@media(max-width: 768.9px) {
	.related-post .container ul li {
		width: 350px;
	height: 300px;
	}
}
@media(max-width: 425.9px) {
		.related-post .container h2 {
			font-size: 28px;
		}
}

@media(max-width: 375.9px) {
	.related-post .container ul li {
		width: 100%;
		margin-left: 0;
	}
}
footer {
	margin-top: 100px;
	width: 100%;
	height: 80px;
	background: #343A40;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-left: 70px;
	padding-right: 70px;
}
footer p {
	color: #E5E5E5;
	font-size: 14px;
}
footer a {
	color: #E5E5E5;
	font-size: 14px;
}
.li_nav_active {
	border-bottom: 2px inset yellow;
}
main {
	display: flex;
  align-items: center;
}
main .container {
	display: flex;
	justify-content: center;
	text-align: center;
	align-items: center;
}
.main_block {
	max-width: 538px;
	
}
main .container p:first-child {
	display: inline-block;
	width: 70px;
	color: white;
	flex: none;
   order: 0;
   flex-grow: 0;
   font-size: 10px;
   padding: 5px 5px 5px 5px;
   background-color: rgba(255, 255, 255, 0.3);
   border-radius: 4px;
}
main .container h2 {
	font-size: 36px;
	color: #FFFFFF;
	text-shadow: 1.5px 1.5px 1.5px #000;
	margin-top: 13px;
}
.main_block p:nth-child(3) {
	font-size: 12px;
	color: #E5E5E5;
	text-shadow: 1px 1px 1px #000;
	margin-top: 15px;
}
.main_block p:last-child {
	color: #FFFFFF;
	font-size: 12px;
	text-shadow: 1px 1px 1px #000;
	margin-top: 20px;
}
.comment {
	margin-top: 70px;
}
.comment .container {
	max-width: 900px;
}
.comment ul {
	display: flex;
	justify-content: space-between;
}
.comment li:first-child {
	color: rgba(30, 30, 30, 1.0);
	font-size: 12px;
}
.comment-li {
	max-width: 700px;
}
.comment-li p {
	color: #495057;
	font-size: 14px;
}
.comment-li p:first-child {
	margin-bottom: 20px;
}
.comment-header {
	color: #D4A373;
	font-size: 36px;
	margin-top: 20px;
	margin-bottom: 20px;
}
.comment_div {
	display: flex;
	align-items: center;
	margin-top: 40px;
}
.comment_div img {
	margin-right: 20px;
}
.comment_div div h4 {
	color: #343A40;
	font-size: 12px;
}
.comment_div div h4 span {
	color: #495057;
}
.comment_div div p {
	font-size: 12px;
}

@media(max-width: 768.9px) {
	.comment-li {
	max-width: 500px;
}
}
@media(max-width: 425.9px) {
	.comment ul {
		display: block;
	}
	.comment-li {
		max-width: 400px;
	}
	.comment li:first-child {
		font-size: 12px;
		margin-bottom: 20px;
	}
	.comment-header {
		font-size: 28px;
	}
	.comment-li p {
		font-size: 11px;
	}

}